	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Mirow Blog, Revisited","date":"13 Feb 2004","slug":"/2004-02-13-mirow-blog-revisited/","tags":"Y2004"},"html":"<p>Here's a follow up on my <a title=\"PapaScott: Mirow Blog\" href=\"http://www.papascott.de/2004/01/24/2819.php\">post last month</a> on the weblog of SPD leading candidate in Hamburg, <a title=\"MirowFuerHamburg.de: Treffpunkt für Unterstützer\" href=\"http://www.mirowfuerhamburg.de/\">Thomas Mirow</a>. They've fixed some of the things I complained about in my first post. There's now a big link to the blog from the <a title=\"Thomas Mirow - Bürgermeister für Hamburg\" href=\"http://www.thomasmirow.de/mirow/start.html\">campaign's main web site</a>. The posters at the blog now call themselves \"Team Mirow\" instead of pretending to be Thomas Mirow... so they're still anonymous, but at least they are being honest about it. And the weblog now bills itself as a 'meeting place for supporters', which means the weblog has a clear and achievable focus. So did the makers of the weblog read my post and take my comments seriously? Well, maybe <a title=\"Noch'n Blogg\" href=\"http://blogg.lumma.de/\">one</a> of them did. Is that the reason that Red/Green has <a title=\"Wahl in Hamburg: Umfrage: Aufwärtstrend bei rot-grün\" href=\"http://interferno.org/wahlinhamburg/archiv/000102.html\">pulled even</a> with the CDU in the latest polls? I doubt it.</p>"},"next":{"fields":{"title":"LazyWeb MT Plugins","date":"12 Feb 2004","slug":"/2004-02-12-lazyweb-mt-plugins/"}},"previous":{"fields":{"title":"","date":"14 Feb 2004","slug":"/2004-02-14-abendblatt-diaet-rausch-so-nimmt-amerika-ab-how-america-loses-weight/"}}},{"node":{"fields":{"title":"LazyWeb MT Plugins","date":"12 Feb 2004","slug":"/2004-02-12-lazyweb-mt-plugins/","tags":"Y2004"},"html":"<p>Our <a title=\"Lüllauer Gegen-Wind\" href=\"http://www.luellauergegenwind.de/\">wind generator initiative</a> is starting to post to the website (made with with Movable Type), and I've noticed a couple of things for which I wish I had a <a title=\"MT Plugin Directory\" href=\"http://mt-plugins.org/\">plugin</a>. For one thing, some people tend to copy and paste long text  from Word or from Outlook. (I try to tell them to link instead of copy, but they're volunteers and new at this.) The text tends to be word-wrapped with newlines within the paragraphs, which MT not so helpfully converts to line breaks. The otherwise excellent <a title=\"Brad Choate: Textile\" href=\"http://www.bradchoate.com/mt-plugins/textile\">Textile</a> plugin does the same thing. It would be nice to be able to turn convert-to-line-breaks on or off for individual posts, and still have paragraphs created with &lt;p&gt; tags. </p>\n<p>The other thing is that these long posts are awkward for archive pages. Yes, MT lets you enter an Extended Entry or an Excerpt, but I don't want to bother our volunteers with extra fields. And using FirstNWords tends to cut  sentences off in the middle of.... It would be nice if there were a tag like MTEntryFirstParagraph, which I could put in an archive template and know I'll also have complete sentences on the archive page.</p>\n<p>There may already be easy ways to do this, and if not I could always write something myself. But by posting this, maybe somebody will have a pointer and save me the work of finding or writing it myself. :-)</p>"},"next":{"fields":{"title":"Rainy Day: Editors blog, 'Practical issues and real solutions for working editors and senior newsroom executives'","date":"11 Feb 2004","slug":"/2004-02-11-rainy-day-editors-blog-practical-issues-and-real-solutions-for-working-editors-and-senior-newsroom-executives/"}},"previous":{"fields":{"title":"Mirow Blog, Revisited","date":"13 Feb 2004","slug":"/2004-02-13-mirow-blog-revisited/"}}},{"node":{"fields":{"title":"Rainy Day: Editors blog, 'Practical issues and real solutions for working editors and senior newsroom executives'","date":"11 Feb 2004","slug":"/2004-02-11-rainy-day-editors-blog-practical-issues-and-real-solutions-for-working-editors-and-senior-newsroom-executives/","tags":"Y2004"},"html":"<p><a title=\"Blogging _is_ journalism. Next question?\" href=\"http://www.eamonn.com/archives/001227.html\">Rainy Day:</a> <a title=\"Blogging _is_ journalism. Next question?\" href=\"http://wef.blogs.com/editors/\">editorsblog.org, 'Practical issues and real solutions for working editors and senior newsroom executives'</a></p>"},"next":{"fields":{"title":"Gone Home","date":"11 Feb 2004","slug":"/2004-02-11-gone-home/"}},"previous":{"fields":{"title":"LazyWeb MT Plugins","date":"12 Feb 2004","slug":"/2004-02-12-lazyweb-mt-plugins/"}}}],"pathPrefix":"","first":false,"last":false,"index":531,"pageCount":1101}}

